body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #202e4b;
  min-height: 100%;
}

.App-header {
  background-color: #d7dae0;
  min-height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(15, 14, 14);
}

table {
  background-color: #202e4b;
  min-height: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
td.first {
  width: 240px;
}
td.VZ-01-01 {
  width: 190px;
}
td.VZ-01-02 {
  width: 180px;
}
td.VZ-02-02 {
  width: 180px;
}
td.VZ-01-03 {
  width: 190px;
}

td.levels, td.stock {
  width: 200px;
  border: #d7dae0;
  border-width: 1px;
} 

tbody {
  width: 1212px;
}

tbody.stockInfo td {
  width: 260px;
}

tr {
  min-width: 1212px;
}

p {color: white;}

