.App {
  text-align: center;
  background-color: #202e4b;
  min-height: 100%;
}

.App-header {
  background-color: #d7dae0;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(15, 14, 14);
}

table {
  background-color: #202e4b;
  min-height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
td.first {
  width: 240px;
}
td.VZ-01-01 {
  width: 190px;
}
td.VZ-01-02 {
  width: 180px;
}
td.VZ-02-02 {
  width: 180px;
}
td.VZ-01-03 {
  width: 190px;
}

td.levels, td.stock {
  width: 200px;
  border: #d7dae0;
  border-width: 1px;
} 

tbody {
  width: 1212px;
}

tbody.stockInfo td {
  width: 260px;
}

tr {
  min-width: 1212px;
}

p {color: white;}
